import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import SideNav, { NavItem, NavIcon, NavText } from "@trendmicro/react-sidenav";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import Home from "../home";
import About from "../about";

class Admin extends Component {
  render() {
    return (
      <div className="ml-5" style={{ minHeight: "100vh" }}>
        <Router>
          <Route
            render={({ location, history }) => (
              <React.Fragment>
                <SideNav
                  onSelect={selected => {
                    const to = "/admin/" + selected;
                    if (location.pathname !== to) {
                      history.push(to);
                    }
                  }}
                >
                  <SideNav.Toggle />
                  <SideNav.Nav defaultSelected="home">
                    <NavItem eventKey="home">
                      <NavIcon>
                        <i
                          className="fa fa-fw fa-home"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Home</NavText>
                    </NavItem>
                    <NavItem eventKey="devices">
                      <NavIcon>
                        <i
                          className="fas fa-wifi"
                          style={{ fontSize: "1.75em" }}
                        />
                      </NavIcon>
                      <NavText>Devices</NavText>
                    </NavItem>
                  </SideNav.Nav>
                </SideNav>
                <main className="w-auto ml-1">
                  <Route path="/admin/" exact component={props => <Home />} />
                  <Route path="/admin/home" component={props => <Home />} />
                  <Route path="/admin/devices" component={props => <About />} />
                </main>
              </React.Fragment>
            )}
          />
        </Router>
      </div>
    );
  }
}

export default Admin;
